import React from "react";
//import Pdf from "../../editable-stuff/resume.pdf";
import leaflet from "../../assets/img/leaflet.png"

import axios from "axios";

const pictureLinkRegex = new RegExp(
  /[(http(s)?):(www.)?a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/
);

const AboutMe = ({ heading, message, link, imgSize, file }) => {
  const [profilePicUrl, setProfilePicUrl] = React.useState("");
  const [showPic, setShowPic] = React.useState(Boolean(link));

  React.useEffect(() => {
    if (link && !pictureLinkRegex.test(link)) {
      handleRequest();
    } else {
      setProfilePicUrl(link);
    }
  }, [link]);

  const handleRequest = async () => {
    const instaLink = "https://www.instagram.com/";
    const instaQuery = "/?__a=1";
    try {
      const response = await axios.get(instaLink + link + instaQuery);
      setProfilePicUrl(response.data.graphql.user.profile_pic_url_hd);
    } catch (error) {
      setShowPic(false);
      console.error(error.message);
    }
  };

  return (
    <div id="about" className="jumbotron jumbotron-fluid m-0 bg-white" >
      <div className="container container-fluid">
        <div className="row">
          <div className={`col-lg-12`}>
            <h2 className="display-4 mb-5 text-center">{heading}</h2>
            <p className="lead text-left">{message}</p>
            <p className="lead text-center">
              <a
                className="btn btn-outline-dark btn-lg"
                href={leaflet}
                download="전시 리플렛_부산에 괴물이 산다"
                //target="_blank"
                rel="noreferrer noopener"
                role="button"
                aria-label="leaflet"
                file
              >
                자세히 보기
              </a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
